<template>
  <section class="deposit-page">
    <TextHeader>
      {{ title }}
    </TextHeader>
    <Wizard
      :model-value="currentStep"
      :steps="wizardSteps"
      @update:modelValue="setCurrentStep"
    />
    <component
      :is="'Step' + (currentStep + 1)"
      @next="setCurrentStep"
    />
  </section>
</template>

<script>
import { computed, defineAsyncComponent, reactive, ref } from 'vue'
import { onBeforeRouteLeave, useRoute, useRouter } from 'vue-router'
import { useI18n } from '@/shared/plugins/i18n'
import TextHeader from '@/client/components/TextHeader'
import Wizard from '@/shared/components/UIKit/Wizard/Wizard'
import Button from '@/shared/components/UIKit/Button'
import Input from '@/shared/components/UIKit/Input'
import Icon from '@/shared/components/Icon/Icon'
import { useMutations } from '@/shared/plugins/vuex'
import { MODULES_NAMES } from '@/shared/common/modules'

export default {
  name: 'DepositPage',
  components: {
    Step1: defineAsyncComponent(() => import('@/client/views/DepositPage/Steps/Step1')),
    Step2: defineAsyncComponent(() => import('@/client/views/DepositPage/Steps/Step2')),
    Step3: defineAsyncComponent(() => import('@/client/views/DepositPage/Steps/Step3')),
    Icon,
    Button,
    Input,
    Wizard,
    TextHeader
  },
  setup () {
    const route = useRoute()
    const router = useRouter()
    const { t } = useI18n()
    const {
      SET_TOOLS: setTools
    } = useMutations(MODULES_NAMES.TOOLS)

    const currentStep = ref(Number(route.query.step) || 0)
    const email = ref('')
    const emailPlaceholder = ref('notifyme@myemail.com')

    const wizardSteps = reactive([
      { title: t('depositPage.awaitingDeposit'), id: 1, color: '246, 226, 102' },
      { title: t('depositPage.processing'), id: 2, color: '212, 131, 67' },
      { title: t('depositPage.sending'), id: 3, color: '68, 190, 73' }
    ])
    const title = computed(() => {
      if (route.query.error) {
        return t('depositPage.onHold')
      }
      return `${wizardSteps[currentStep.value].title} (${currentStep.value + 1}/${wizardSteps.length})`
    })

    const setCurrentStep = (step) => {
      currentStep.value = step
      router.replace({
        ...route,
        query: { ...route.query, step }
      })
    }

    onBeforeRouteLeave(() => {
      setTools([])
    })

    return {
      t,
      title,
      wizardSteps,
      currentStep,
      email,
      emailPlaceholder,
      setCurrentStep
    }
  }
}
</script>

<style lang="scss">
.deposit-page {
  padding: 40px 60px 70px 60px;

  .wizard {
    margin-bottom: 90px;
  }

  @media screen and (max-width: map-get($mobileLayout, "MaxWidth")) {
    padding: 30px 20px 40px 20px;

    .wizard {
      margin-bottom: 73px;
    }
  }
}
</style>
